.k-scheduler-head{
  display: none;
}

.loadingHeight {
  height: calc(100vh - 305px);
}
.divTableHeight {
  overflow-x: auto;
  overflow-y: auto;
  height: calc(100vh - 305px);
}

.allDayLabel{
  color:#fff;
}
.allDayScore{
  color:#fff;
  float:right;
}

.calendarHeight {
  height: calc(100vh - 250px);
  overflow: scroll;
}

.saveButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  color: #ffffff;
  background-color: #008662;
  border: none;
  border-radius: 4px;
}
.saveButton:disabled {
  background-color: #b1b1b1;
  cursor: not-allowed;
}

.cancelButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  background-color: #f6f6f6;
  color: #5c636a;
  border: none;
  border-radius: 4px;
}

.divScheduleScores{
  display: block;
  color: #6D6D73;
  margin-top: 10px;
  width: calc(100vw - 325px );
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.lblTitleCalendar{
  overflow: hidden;
  display: block;
  line-height: 15px;
  min-height: 15px;
  max-height: 30px;
  text-overflow: ellipsis;
  font-size: 14px;
  width:100%; 
  padding-right: 6px;
}

.lblTitleCalendarNoWrap{
  white-space: nowrap;
  overflow: hidden;
  display: block;
  text-overflow: ellipsis;
  font-size: 14px;
  width:122px;
}

.lblTimeStampCalendar{
  color: #6D6D73;
  font-size: 12px;            
  width: 150px;        
}

.lblTimeStampCalendarNoWrap{
  color: #6D6D73;
  font-size: 12px;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
}

.divParent{
  position:relative;
   height:100%;
   z-index: auto !important;
}

.divChild{
  position: relative;
    top: 45%;
    left: 15%;
    transform: translate(0%, -30%);
    overflow: hidden;
    height: 70%;
    width: 85%;
}
.faCircleNotchSpin {
  font-size: 50px;
  color: #13ad83;
}
.searchWrapper {
  display: flex;
  width: 100%;
  margin-bottom: 5px;
}
.search {
  height: 28px;
  width: 100%;
  border: 1px solid #b1b1b1;
  border-right: none;
  border-radius: 4px 0px 0px 4px;
  font-size: 12px;
  text-indent: 5px;
}

.search:focus {
  outline: none;
}

.timesIcon {
  content: url('../../../assets/icons/times.svg');
  width: 12.7px;
  height: auto;
  margin-top: 6px;
  margin-left: 10px;
  cursor: pointer;
}

.searchIcon {
  content: url('../../../assets/icons/magnifying-glass.svg');
  margin-top: 6px;
  margin-left: 5px;
  cursor: pointer;
}

.timesIcon:hover,
.searchIcon:hover {
  filter: brightness(0) saturate(100%) invert(30%) sepia(86%) saturate(1538%)
    hue-rotate(142deg) brightness(87%) contrast(101%);
}

.timesIconWrapper {
  width: 30px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-left: none;
  border-right: none;
}

.searchIconWrapper {
  width: 30px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-left: none;
  border-radius: 0px 4px 4px 0px;
}

.schedulerIcons{
 float: right;
 cursor: pointer;
}

.dropdown {
  position: absolute;
    right: 1px;
    z-index: 2;
  width: 10px;
}

.dropdownContent {
  display: block;
  position: absolute;
  background-color: #f9f9f9;
  width: 100px;
  right: 0;
  z-index: 3;
  overflow-y:auto;
  border-radius: 0px 0px 4px 4px;
  border: 1px solid #ced4da;
}

.dropdownItem {
  width: 100%;
  display: block;
  padding-left:10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: left;
}

.dropdownItem:hover {
  background-color: #008662;
}

.toggleDropdown{
  float: right;
}

.txtGradeCertificate{
  color: #787E82;
  font-size: 12px;
  display: block;
  overflow: hidden; 
  line-height: 15px;
  min-height: 15px;
  max-height: 30px;
  text-overflow: ellipsis;
  width: 100%;
}

.floatRight{
  float: right;
}

.headerIcons {
  margin-top: 5px;
  cursor: pointer;
}